import { Formik, Form } from 'formik';
import { FieldStyled, ErrMessage } from './TutorForm.styled';
import { object, string } from 'yup';
import { Button } from 'components';
import { createTutorsAction } from '../../store/tutors/actions';
import { useDispatch } from 'react-redux';

const fieldsData = [
  { name: 'lastName', label: '' },
  { name: 'firstName', label: '' },
  { name: 'patronymic', label: '-' },
  { name: 'phone', label: '' },
  { name: 'email', label: '' },
  { name: 'city', label: '' },
];

const validationSchemaForm = object().shape({
  firstName: string().required('  '),
  lastName: string()
    .min(2, '   ')
    .max(9, '   ')
    .required(),
  patronymic: string().required(),
  phone: string().required(),
  email: string().required(),
  city: string().required(),
});

const TutorForm = () => {
  const dispatch = useDispatch();

  const initialValues = {
    firstName: '',
    lastName: '',
    patronymic: '',
    phone: '',
    email: '',
    city: '',
  };

  const handleSubmitForm = (values, { setSubmitting, resetForm }) => {
    setSubmitting(true);
    //  console.log(' ~ values', values);
    dispatch(createTutorsAction(values));
    resetForm();
    setSubmitting(false);
  };

  return (
    <Formik
      validationSchema={validationSchemaForm}
      initialValues={initialValues}
      onSubmit={handleSubmitForm}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        isSubmitting,
        /* and other goodies */
      }) => (
        <Form>
          <h3> </h3>
          {fieldsData.map(({ name, label }) => {
            return (
              <div key={name}>
                <FieldStyled
                  type="text"
                  id={name}
                  name={name}
                  placeholder={label}
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values[name] || ''}
                />
                <ErrMessage component="div" name={name} />
              </div>
            );
          })}

          <Button text="add" type="submit" />
        </Form>
      )}
    </Formik>
  );
};

export default TutorForm;
